import Nav from "../nav/nav"
import Lists from "../lists/lists"
export default {
	data() {
		return {
			longitude: 0,
			latitude: 0,
			speed: 0,
			accuracy: 0,
			covers: [],
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			selectedIndex: 0,
			items: [
				{
					name: "美食",
					keyword: "美食",
					type: "error",
					select: false
				},
				{
					name: "景点",
					keyword: "景点",
					type: "success",
					select: false
				},
				{
					name: "停车场",
					keyword: "停车场",
					type: "primary",
					select: false
				},
				{
					name: "水果",
					keyword: "水果",
					type: "error",
					select: false
				},
				{
					name: "公园",
					keyword: "公园",
					type: "warning",
					select: false
				},
				{
					name: "小吃",
					keyword: "小吃",
					type: "error",
					select: false
				},
				{
					name: "酒店",
					keyword: "酒店",
					type: "primary",
					select: false
				},
				{
					name: "住宅区",
					keyword: "住宅区",
					type: "default",
					select: false
				},
				{
					name: "学校",
					keyword: "学校",
					type: "default",
					select: false
				},
			],
		}
	},
	onLoad() {
		var that=this
		wx.showLoading({
		  title:"定位中",
		  mask:true
		})
		wx.getLocation({
		  type: 'gcj02',
		  altitude:true,//高精度定位
		  //定位成功，更新定位结果
		  success: function (res) {
			var latitude = res.latitude
			var longitude = res.longitude
			var speed = res.speed
			var accuracy = res.accuracy
		   
			that.longitude = longitude
			that.latitude = latitude,
			that.speed = speed,
			that.accuracy = accuracy
			that.$store.dispatch("onMapReady");
		  },
		  //定位失败回调
		  fail:function(){
			wx.showToast({
			  title:"定位失败",
			  icon:"none"
			})
		  },
	 
		  complete:function(){
			//隐藏定位中信息进度
			wx.hideLoading()
		  }
	 
		})
	},
	methods: {
		aimPlayer() {
			let map = wx.createMapContext('mainMap');
			map.moveToLocation();
		},
		startNav() {
			uni.openLocation({
				latitude: 25.0271210000,
				longitude: 101.7632820000,
				success: function() {
					console.log('success');
				}
			});
		},
		onSelectNav(index) {
			var that = this;
			wx.showLoading({
			  title:"加载中..",
			  mask:true
			})
			this.items[this.selectedIndex].select = false;
			this.items[index].select = true;
			this.selectedIndex = index;
			this.$store.dispatch("changeTag",{
				tag : this.items[index].keyword,
				func : function() {
					that.goTop();
					wx.hideLoading();
				},
			});
		},
		scroll(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		goTop: function(e) {
			// 解决view层不同步的问题
			this.scrollTop = this.old.scrollTop
			this.$nextTick(function() {
				this.scrollTop = 0
			});
		}
	},
	computed: {
		getMarkers: function() {
			return this.$store.getters.getMarkers;
		}
	},
	components: {
		Nav,
		Lists,
	}
}
